.auth-container{
    min-width: 374px;
    background: #222A32;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @media (max-width: 768px) {
        min-width: 100%;
        padding: 0 15px;
    }
}
.password-reset{
    margin-left: 24px;
    margin-top: 48px;

    @media (max-width: 1024px) {
        margin-bottom: 50px;
    }
}
.reset-password-text{
    color: white;
}
a{
    text-decoration: none;
}
.auth-content{
    display: flex;
    align-items: flex-start;
    width: 100%;
    background-color: #222A32;
}
.auth-background{
    background-image: url('../../../icons/SignIn.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: calc(100% - 374px);
    height: 100vh;
}

.mobile-reg-link{
    display: none;
    margin-bottom: 0;

    @media (max-width: 1024px) {
        display: block;
    }
}