.hr{
    width: 100%;
    height: 1px;
    background: linear-gradient(to right, #8698A2 0%, #222A32 100%);
}
.reg-form-block{
    margin: 48px auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 24px;
}
.reg-form{
    margin: 48px 0px;
}
.accept-terms-conditions{
    width: 326px;
    display: flex;
    margin-top: 24px;
    align-items: center;
}
.checkbox-text{
    margin: 0px 0px 0px 17px;
    font-size: 12px;
    color: #8698A2;
    line-height: 14px;
}
.accept-terms-checkbox{
    transform: scale(1.5);
}
.terms-btn{
    color: #31CDC9;
    cursor: pointer;
}
.registration-auth-form-control{
    width: 100%;
}
.reg-submit {
    width: 100%;
}