.modal-container{
    bottom: 0;
    left: 0;
    position: fixed;
    width: 100%;
    z-index: 1;
    padding: 0 24px 48px 24px;
    display: flex;
}
.modal-content{
    align-items: center;
    display: flex;
    height: 100%;
    justify-content: space-between;
    max-width: 386px;
    width: 100%;
    border-radius: 12px;
    background: #19212a;
    padding: 12px 24px;
    box-shadow: 0px 4px 8px 0 #000;
    p{
        margin: 5px 0px 5px 15px;
    }
}
.accept-icon{
    background: #00FF66;
    width: 28px;
    height: 28px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    font-size: 18px;
}
.success-text{
    font-size: 16px;
    color: white;
}
.success-secondary-text{
    font-size: 14px;
    color: #8698A2;
}
.modal-left-side{
    display: flex;
    align-items: center;
}
.close-modal{
    color: #8698A2;
    cursor: pointer;
}