.nft-item-out {
    padding: 0 8px;
    width: 100%;

    @media (max-width: 1024px) {
        min-width: 200px;
    }
}

.nft-item{
    width: 100%;
    // height: 294px;
    border-radius: 12px;
    background: #19212A;
    margin-top: 24px;
}
.nft-image{
    width: 100%;
    height: 200px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
}
.nft-name{
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    color: white;
    margin: 0;
}
.nft-price{
    color: #31CDC9;
    font-size: 14px;
    text-align: center;
    margin: 0;
}
.nft-info{
    height: 94px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
}